import React, { Component, useState } from 'react';
import LAutoComplete from '../components/RAutoComplete';
import data from '../../src/utilities/stations.json';
import Button from '@mui/material/Button';
import RAppbar from '../components/RAppBar';
import { Grid } from '@mui/material';
import TrainCard from '../components/RCard';
import APIUrls from '../utilities/APIUrls.json';
import RAlert from '../components/RAlert';

export default function Main(props) {
    const [fromStation, setFromStation] = useState('');
    const [toStation, setToStation] = useState('');
    const [openFrom, setOpenFrom] = useState(false);
    const [openTo, setOpenTo] = useState(false);
    const [filteredOptions, setFilteredOptions] = useState([]);
    const [traindata, setTrainData] = useState('');

    const handleInputFromChange = (event) => {
        const inputValue = event.target.value;
        setFromStation(inputValue);
        setOpenFrom(inputValue !== '');
        const filteredData = data.data.filter(option => option.label.toLowerCase().includes(inputValue.toLowerCase()));
        setFilteredOptions(filteredData);
    };

    const handleInputToChange = (event) => {
        const inputValue = event.target.value;
        setToStation(inputValue);
        setOpenTo(inputValue !== '');
        const filteredData = data.data.filter(option => option.label.toLowerCase().includes(inputValue.toLowerCase()));
        setFilteredOptions(filteredData);
    };


    const handleFromStationChange = (event, value) => {
        setFromStation(value ? value.value : '');
        setOpenFrom(false);
    };

    const handleToStationChange = (event, value) => {
        setToStation(value ? value.value : '');
        setOpenTo(false);
    };

    const handleSubmit = () => {
        if(fromStation !== '' && toStation !== ''){
            getData(fromStation, toStation);
            <RAlert open={false} />
        }
        else{
            <RAlert message="Please fill the Station field" open={true} />
        }
    };

    const getData = (from, to) => {
        var requestOptions = {
            method: 'GET',
            redirect: 'follow',
        };

        fetch(APIUrls.baseURL + APIUrls.getTrainsFrom + from + APIUrls.getTrainsTo + to + APIUrls.dataSourceandLanguage, requestOptions)
            .then(response => response.text())
            .then(result => {
                const formattedData = formatTrainData(result);
                formattedData.splice(0, 1);
                setTrainData(formattedData);
            })
            .catch(error => console.log('error', error));
    };

    function formatTrainData(data) {
        const schedules = [];
        const trainDetails = data.split('^');

        trainDetails.forEach(trainDetail => {
            const trainFields = trainDetail.split('~');
            const train = {
                train_number: trainFields[0],
                train_name: trainFields[1],
                origin: trainFields[2],
                destination: trainFields[4],
                departure_time: trainFields[10],
                arrival_time: trainFields[11],
                travel_time: trainFields[12],
                operating_days: trainFields[19],
                availability: trainFields[24],
                berth_info: trainFields[25]
            };
            schedules.push(train);
        });
        return schedules;
    }

    const handleReset = () => {
        setFromStation('')
        setToStation('')
        setTrainData('')
    }

    return (
        <div>
            <RAppbar />
            <Grid container spacing={2}>
                <Grid item xs={12} sm={12} lg={4} md={4}>
                </Grid>
                <Grid item xs={12} sm={12} lg={4} md={4} sx={{ marginTop: '10px' }}>
                    <div style={{ display: 'block', marginLeft: '10px' }}>
                        <h3>Search Trains</h3>
                        <LAutoComplete label={"From Station"} inputValue={fromStation} filteredOptions={filteredOptions} open={openFrom} handleInputChange={(e) => handleInputFromChange(e)} handleOptionChange={(event, value) => handleFromStationChange(event, value)} />
                        <LAutoComplete label={"To Station"} inputValue={toStation} filteredOptions={filteredOptions} open={openTo} handleInputChange={(e) => handleInputToChange(e)} handleOptionChange={(event, value) => handleToStationChange(event, value)} />
                        <Button style={{ float: 'right', marginRight: '20px' }} variant="contained" onClick={() => handleSubmit()} >Search</Button>
                        <Button style={{ float: 'right', marginRight: '20px', backgroundColor: 'red' }} variant="contained" onClick={() => handleReset()} >Reset</Button>
                    </div>
                </Grid>
                <Grid item xs={12} sm={12} lg={4} md={4}>
                </Grid>
                <Grid item xs={12} sm={12} lg={4} md={4}>
                </Grid>
                <Grid item xs={12} sm={12} lg={4} md={4} sx={{ margin: '10px' }}>
                    {traindata !== '' ? traindata.map(item => (
                        <div style={{ display: 'block', margin: '10px', border: '1px solid black' }}>
                            <TrainCard
                                trainNo={item.train_number}
                                name={item.train_name}
                                fromStation={item.origin}
                                toStation={item.destination}
                                travelTime={Math.round(item.travel_time) + " hrs"}
                                departTime={item.departure_time}
                                arrivalTime={item.arrival_time}
                            />
                        </div>
                    ))
                        :
                        ''}
                </Grid>
                <Grid item xs={12} sm={12} lg={4} md={4}>
                </Grid>
            </Grid>
        </div>
    )
}